:root {
  --gemini-primary: #1a73e8;
  --gemini-secondary: #f0f0f5;
  --background-color: #fff;
  --border-color: #dadce0;
  --text-color: #202124;
  --user-bubble: #e8f0fe;
  --ia-bubble: #f1f3f4;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  margin: 0;
  padding: 20px;
  font-family: Roboto, sans-serif;
  display: flex;
}

#app-container {
  background: var(--background-color);
  border-radius: 12px;
  width: 100%;
  max-width: 800px;
  padding: 0;
  box-shadow: 0 4px 12px #0000001a;
}

h1 {
  text-align: center;
  color: var(--gemini-primary);
  border-bottom: 1px solid var(--border-color);
  margin: 0;
  padding: 20px 0 10px;
  font-size: 1.5rem;
}

#chat-container {
  flex-direction: column;
  height: 70vh;
  min-height: 500px;
  padding: 0 20px;
  display: flex;
  overflow: hidden;
}

#chat-window {
  scroll-behavior: smooth;
  flex-grow: 1;
  padding: 20px 0;
  overflow-y: auto;
}

#chat-window::-webkit-scrollbar {
  width: 8px;
}

#chat-window::-webkit-scrollbar-thumb {
  background-color: var(--border-color);
  border-radius: 4px;
}

.message {
  word-wrap: break-word;
  border-radius: 20px;
  max-width: 80%;
  margin-bottom: 15px;
  padding: 10px 15px;
  line-height: 1.6;
  box-shadow: 0 1px 1px #0000000d;
}

.user-message {
  background-color: var(--user-bubble);
  color: var(--text-color);
  border-bottom-right-radius: 4px;
  margin-left: auto;
}

.system-message, .ia-message {
  background-color: var(--ia-bubble);
  color: var(--text-color);
  border-bottom-left-radius: 4px;
  margin-right: auto;
}

#input-area {
  border-top: 1px solid var(--border-color);
  gap: 10px;
  padding: 15px 0 20px;
  display: flex;
}

#user-input {
  border: 1px solid var(--border-color);
  border-radius: 25px;
  outline: none;
  flex-grow: 1;
  padding: 12px 18px;
  font-size: 1rem;
  transition: border-color .3s;
}

#user-input:focus {
  border-color: var(--gemini-primary);
  box-shadow: 0 0 0 2px #1a73e833;
}

#send-button {
  background-color: var(--gemini-primary);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  transition: background-color .3s, transform .1s;
  display: flex;
}

#send-button:hover {
  background-color: #1558b3;
}

#send-button:active {
  transform: scale(.95);
}

.send-icon {
  font-size: 1.2rem;
  transform: translateX(2px);
}
